import { NgModule } from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { AppComponent } from './app.component';
import {GymModule} from "./gym/gym.module";
import { RouterModule }   from '@angular/router';
import {GymMainComponent} from "./gym/gym-main.component";
import {GymIntroComponent} from "./gym/gym-intro.component";
import {GymRoutingModule} from "./gym/gym-routing-module";
import {NgReduxModule} from "ng2-redux";
@NgModule({
  imports: [
    BrowserModule, GymModule,
    GymRoutingModule , NgReduxModule
  ],
  declarations: [
    AppComponent
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule { }


