import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {GymAboutUsComponent} from "./gym-about-us.component";
import {GymIntroComponent} from "./gym-intro.component";
import {GymContactUsComponent} from "./gym-contact-us.component";


const routes: Routes = [
    { path: '', redirectTo: '/intro', pathMatch: 'full' },
    { path: 'intro',  component: GymIntroComponent},
    { path: 'about-us', component: GymAboutUsComponent },
    { path: 'contact-us', component: GymContactUsComponent },
    //{ path: '**', redirectTo: '/intro', pathMatch: 'full' }
];
@NgModule({
    imports: [ RouterModule.forRoot(routes) ],
    exports: [ RouterModule ]
})
export class GymRoutingModule {}



