import Immutable = require('immutable');

export interface PageState {
    currentPage: string,
    pagingHistory: string[]
};

const initialPageState: PageState = {
    currentPage: "initial state page 0",
    pagingHistory: []
};


export const pageReducer = function (state = initialPageState, action: any) {
    switch (action.type) {
        case "paging":
            return updatePage(state, action);
        default :
            return state;
    }
}

function updatePage(state = initialPageState, action: any) {
    //Object.freeze(state);
    let newState = state;
    var currentPage = action.page;
    if (state.currentPage !== currentPage) {
        newState = Object.assign({}, state , {currentPage: currentPage, pagingHistory: state.pagingHistory.concat(currentPage)})
    }

    return newState;
}


