import {NgRedux} from "ng2-redux";
import {Injectable} from "@angular/core";
import {TextBoxState} from "../store/text-box.reducer";

@Injectable()
export  class TextBoxActions {
    constructor (private ngRedux: NgRedux<TextBoxState> ){}

    texting(text:string ) : void {
        this.ngRedux.dispatch(<any>{type:"textChange" , text: text});
        console.log(this.ngRedux.getState());
    }
}


