import {NgModule} from "@angular/core";
import {GymMainComponent} from "./gym-main.component";
import {GymHeaderComponent} from "./gym-header.component";
import {GymIntroComponent} from "./gym-intro.component";
import {GymAboutUsComponent} from "./gym-about-us.component";
import {GymContactUsComponent} from "./gym-contact-us.component";
import {GymRoutingModule} from "./gym-routing-module";
import {BrowserModule} from "@angular/platform-browser";
import {PageActions} from "../actions/page.actions";
import {TextBoxActions} from "../actions/text-box.action";
/**
 * Created by hdoryon on 10/13/2016.
 */

@NgModule({
    imports: [GymRoutingModule, BrowserModule],
    declarations: [
        GymMainComponent, GymHeaderComponent, GymIntroComponent, GymAboutUsComponent, GymContactUsComponent
    ],
    exports: [
        GymMainComponent, GymRoutingModule
    ],
    providers : [PageActions, TextBoxActions]
})
export class GymModule {
}


