

export interface TextBoxState {
    currentText: string,
    boxHistory: string[]
};

const initialBoxState: TextBoxState = {
    currentText: "",
    boxHistory: []
};


export const textBoxReducer = function (state = initialBoxState, action: any) {
    switch (action.type) {
        case "textChange":
            return updateTextStateVals(state, action);
        default :
            return state;
    }
}

function updateTextStateVals(state = initialBoxState, action: any) {
    //Object.freeze(state);
    let newState = state;
    let currentText = action.text;
    if (state.currentText !== currentText) {
        newState = Object.assign({}, state , {currentText: currentText, boxHistory: state.boxHistory.concat(currentText)})
    }
    return newState;
}


