import {combineReducers} from "redux";
import {pageReducer, PageState} from "./page.reducer";
import {textBoxReducer, TextBoxState} from "./text-box.reducer";

export interface GymStatus {
    pagingStatus?: PageState,
    textBoxStatus?: TextBoxState
}


export const rootReducer = combineReducers<GymStatus>({
    pagingStatus: pageReducer,
    textBoxStatus: textBoxReducer
});



